.award-page {
    $self: &;
    &__title {
        @extend .event-title;
    }
    &__content {
        @extend .event-wrap;
        display: block;
        padding-bottom: 4rem;
    }
    &__month {
        &:not(:last-child) {
            margin-bottom: 10rem;
        }
        h4 {
            margin-bottom: 4rem;
            color: #fff;
            font-size: 3rem;
            padding: 1.6rem 0;
            text-align: center;
            background: #034172;
            border-radius: 4rem;
            background: linear-gradient(90deg,#034172,#034273 17%,#14778b 50%,#2abfac);
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
                margin-bottom: 2.4rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2.4rem;
            }
        }
    }
    &__prize {
        padding: 0 4.8rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 1rem;
            padding-right: 1rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-left: 1rem;
            padding-right: 1rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
        &:not(:last-child) {
            margin-bottom: 12rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 6.4rem;
            }
        }
        h5 {
            margin-bottom: 2rem;
            display: flex;
            justify-content: center;
            span {
                padding-bottom: 0.6rem;
                color: #ffdb6f;
                font-size: 2.4rem;
                border-bottom: 0.2rem solid #ffdb6f;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.6rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: 1.8rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: 2rem;
                }
            }
        }
        h6 {
            font-size: 1.6rem;
            text-align: center;
            margin-bottom: 1.6rem;
        }
    }
    &__block {
        margin-bottom: 3.2rem;
    }
    &__list {
        display: grid;
        gap: 0.2rem;
        grid-template-columns: repeat(2, 1fr);
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
    }
    &__item {
        display: flex;
        gap: 0.2rem;
        &.waiting {
            #{$self}__number {
                flex-basis: 20%;
            }
            #{$self}__name {
                flex-basis: 40%;
            }
            #{$self}__mobile {
                flex-basis: 40%;
            }
        }
    }
    &__num {
        flex: 1;
        box-sizing: border-box;
        background-color: #fff;
        font-size: 2rem;
        color: #000;
        padding: 0.4rem;
        text-align: center;
        line-height: 1.6;
        display: flex;
        align-items: center;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.4rem;
            flex-basis: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.6rem;
        }
    }
    &__name {
        box-sizing: border-box;
        background-color: #ffdb6f;
        flex: 1;
        font-size: 2rem;
        color: #000;
        padding: 0.4rem;
        text-align: center;
        line-height: 1.6;
        display: flex;
        justify-content: center;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.6rem;
        }
    }
    &__mobile {
        flex: 1;
        box-sizing: border-box;
        background-color: #fff;
        font-size: 2rem;
        color: #000;
        padding: 0.4rem;
        text-align: center;
        line-height: 1.6;
        display: flex;
        align-items: center;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.6rem;
        }
    }
}