:root {
    font-size: 62.5%;
    --mouseX: 50%;
    --mouseY: 50%;
}

html {
    font-family: 'Noto Sans TC', sans-serif, "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    &.is-fixed {
        overflow: hidden;
    }
}

body {
    &.is-fixed {
        overflow: hidden;
    }
    background-color: #000;
}

.rsp-img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}

.main {
    overflow: hidden;
}

.event-all {
    background: #000 url('../images/event-all-bg.jpg') no-repeat center top fixed;
    padding-bottom: 12rem;
    padding-top: 12rem;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        background-size: 200% auto;
        padding-bottom: 0;
        padding-top: 6rem;
        background-attachment: scroll;
    }
}

.award-bg {
    background: #000 url('../images/event-all-bg.jpg') no-repeat center top fixed;
    position: relative;
    padding-bottom: 12rem;
    padding-top: 12rem;
    @include susy-breakpoint($s-320, $c8) {
        background-size: 200% auto;
        padding-bottom: 0;
        padding-top: 6rem;
        background-attachment: scroll;
    }
}

.btn-link {
    width: 26.8rem;
    height: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(187,161,90);
    background: linear-gradient(90deg, rgba(187,161,90,1) 0%, rgba(253,224,143,1) 25%, rgba(220,189,89,1) 50%, rgba(254,237,189,1) 75%, rgba(187,161,90,1) 100%);
    border-radius: 6rem;
    font-size: 2.8rem;
    color: #000;
    text-align: center;
    margin: 0 auto;
    transition: all 300ms ease;
    font-weight: 700;
    @include susy-breakpoint($s-320, $c8) {
        font-size: 2rem;
        width: 22rem;
        height: 5rem;
    }
    &:hover {
        text-decoration: none;
        background-position: 26rem;
    }
}

.btn-top {
    width: 5.2rem;
    height: 5.3rem;
    background: transparent url('../images/icon-gotop@2x.png') no-repeat 0 0;
    background-size: cover;
    position: fixed;
    right: 2rem;
    bottom: 16rem;
    z-index: 500;
    border: 0;
    opacity: 0;
    pointer-events: none;
    transition: all 300ms ease;
    cursor: pointer;
    &.is-active {
        pointer-events: auto;
        opacity: 1;
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 5.2rem * 0.8;
        height: 5.3rem * 0.8;
        right: 1.6rem;
        bottom: 6rem;
    }
}

.rule {
    border-bottom: solid 0.2rem #eaca69;
    padding-bottom: 4rem;
    margin-bottom: 4rem;
    padding-top: 2rem;
}

.rule-title {
    font-size: 2rem;
    color: #ffdb6f;
    @include susy-breakpoint($s-320, $c8) {
        font-size: 1.6rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        font-size: 1.6rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        font-size: 1.8rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        font-size: 2rem;
    }
}

.site-rule {
    list-style: none;
    padding: 0;
    margin: 0;
    counter-reset: ol-list;
    li {
        counter-increment: ol-list;
        font-size: 1.8rem;
        line-height: 1.6;
        padding-left: 2rem;
        position: relative;
        color: #fff;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
            padding-left: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.4rem;
            padding-left: 1.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.6rem;
            padding-left: 1.6rem;
        }
        &:not(:last-child) {
            margin-bottom: 1rem;
        }
        &::before {
            content: counter(ol-list) ". ";
            font-size: 1.8rem;
            color: #fff;
            position: absolute;
            left: 0;
            top: 0;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
            }
        }
    }
}

.event-wrap {
    box-sizing: border-box;
    padding: 3rem 3rem 1rem;
    color: #eaca69;
    width: 100%;
    border: 0.4rem solid;
    border-radius: 2rem;
    background-origin: border-box;
    background-clip: content-box, border-box;
    background: transparent url('../images/content-bg.png') repeat center top;
    display: flex;
    margin-bottom: 2rem;
    @include susy-breakpoint($s-320, $c8) {
        flex-wrap: wrap;
        padding-left: 1.6rem;
        padding-right: 1.6rem;
        border-width: 0.2rem;
        border-radius: 1.2rem;
    }
}

.event-title {
    margin-bottom: 4rem;
    @include susy-breakpoint($s-320, $c8) {
        margin-bottom: 2rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        margin-bottom: 2rem;
    }
    h2 {
        text-align: center;
        line-height: 1;
        margin-bottom: 0;
        span {
            font-family: 'Anton', 'Noto Sans TC';
            font-size: 6rem;
            background: rgb(188,162,85);
            background: linear-gradient(71deg, rgba(188,162,85,1) 0%, rgba(253,225,138,1) 16%, rgba(221,190,80,1) 41%, rgba(255,238,186,1) 65%, rgba(188,162,85,1) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: 900;
            letter-spacing: 0.2rem;
            text-indent: -0.2rem;
            filter: drop-shadow(0 0.6rem 1rem #000);
            position: relative;
            display: inline-block;
            text-align: center;
            margin-bottom: 2rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 7vw;
                letter-spacing: 0;
                text-indent: 0;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 4.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 6.6rem;
            }
        }
    }
    h3 {
        line-height: 1;
        text-align: center;
        margin-bottom: 0;
        span {
            font-family: 'Anton', 'Noto Sans TC';
            font-size: 5.6rem;
            background: rgb(209,209,209);
            background: linear-gradient(90deg, rgba(209,209,209,1) 0%, rgba(235,235,235,1) 27%, rgba(191,191,191,1) 52%, rgba(245,245,245,1) 84%, rgba(255,255,255,1) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: 900;
            letter-spacing: 0.2rem;
            text-indent: -0.2rem;
            filter: drop-shadow(0 0.6rem 1rem #000);
            text-align: center;
            display: inline-block;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 4.8vw;
                letter-spacing: 0;
                text-indent: 0;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 3.2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 4.8rem;
            }
            sup {
                font-size: 0.5em;
                font-weight: 500;
                vertical-align: top;
            }
        }
    }
    // img {
    //     height: 13.8rem;
    //     display: block;
    //     margin: 0 auto;
    //     @include susy-breakpoint($s-320, $c8) {
    //         height: 5.6rem;
    //     }
    //     @include susy-breakpoint($s-768, $c12) {
    //         height: 9rem;
    //     }
    // }
}

.event-block {
    padding-bottom: 12rem;
    @include susy-breakpoint($s-320, $c8) {
        padding-bottom: 4rem;
        margin-bottom: 4rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
    @include susy-breakpoint($s-768, $c12) {
        padding-bottom: 8rem;
    }
}

.event-list {
    display: flex;
    justify-content: center;
    margin-bottom: 3rem;
    margin-left: 0;
    @include susy-breakpoint($s-320, $c8) {
        flex-wrap: wrap;
        margin-bottom: 2rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        margin-bottom: 2rem;
        flex-wrap: wrap;
    }
    @include susy-breakpoint($s-960, $c12) {
        margin-bottom: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        margin-bottom: 2rem;
    }
}

.site-label {
    height: 4.8rem;
    margin-right: 0.8rem;
    @include susy-breakpoint($s-320, $c8) {
        height: 2.8rem;
        margin-right: 0.4rem;
        margin-bottom: 0.4rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        height: 3.2rem;
        margin-right: 0.4rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 4rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        height: 4rem;
    }
}

.text-color-yellow {
    color: #ffdb6f;
}
.text-color-red {
    color: #ff0000;
}
.text-color-green {
    color: #96f037;
    a {
        color: #96f037;
        border-bottom: 0.1rem solid #96f037;
        text-decoration: none;
    }
}

.btn-watch {
    width: 10rem;
    height: 10rem;
    position: fixed;
    right: 2rem;
    bottom: 24rem;
    z-index: 500;
    transition: all 300ms ease;
    img {
        max-width: 100%;
        display: block;
        margin: 0;
    }
    &:hover {
        text-decoration: none;
        background-position: 26rem;
    }
    @include susy-breakpoint($s-320, $c8) {
        right: 1.6rem;
        bottom: 14rem;
        width: 7rem;
        height: 7rem;
    }
}